.Hobby {
  color: black;
  position: absolute;
  font-family: cursive;
  /* font-family: "Permanent Marker"; */
  background-color: honeydew;
  padding: 12px;
  transition: all 0.2s linear;
  border-radius: 15px;
}

.Hobby3 {
  top: 60vh;
  height: 30vh;
  width: 25vw;
  left: 2vw;
}

.Hobby2 {
  top: 33vh;
  height: 30vh;
  width: 25vw;
  left: 37vw;
}
.Hobby1 {
  top: 60vh;
  height: 30vh;
  width: 25vw;
  right: 2vw;
}

.Hobby h1 {
  width: 100%;
  justify-content: center;
  display: flex;
  color: rgb(28, 4, 44);
  font-size: 1.5rem;
}

.Hobby p {
  color: black;
  text-indent: inherit;
  font-size: 1.2rem;
  transition: all 0.6s ease-in-out;
  cursor: default;
}

.Hobby:hover {
  background-color: rgb(247, 225, 247);
  /* transform: scale(1.1); */
  /* box-shadow: 3px 3px 13px rgb(233, 238, 206); */
}

@media(max-width: 1299px){
  .Hobby{
    display: none;
  }
}