.Project1 {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
}

.Info {
  color: black;
  position: absolute;
  font-family: cursive;
  /* font-family: "Permanent Marker"; */
  background-color: honeydew;
  padding: 12px;
  transition: all 0.2s linear;
  border-radius: 15px;
}

.Info1 {
  /* top: 60vh; */
  height: 50vh;
  width: 50vw;
  right: 5vw;
}

.Info h1 {
  width: 100%;
  justify-content: center;
  display: flex;
  color: rgb(28, 4, 44);
  font-size: 1.5rem;
}

.Info p {
  color: black;
  text-indent: inherit;
  font-size: 1.2rem;
  transition: all 0.6s ease-in-out;
  cursor: default;
}

.Info:hover {
  background-color: rgb(247, 225, 247);
  /* transform: scale(1.1); */
  /* box-shadow: 3px 3px 13px rgb(233, 238, 206); */
}

@media (max-width: 1299px) {
  .Info {
    display: none;
  }
}

.MyImg {
  position: absolute;
  left: 12vw;
  border: 2px solid white;
  border-radius: 18px;
  transition: all 1s ease-in-out;
  cursor: pointer;
}

.MyImg img {
  margin: 9px 9px 5px 9px;
  height: 45vh;
  width: 45vh;
}

/* .MyImg:hover{
      background-color: rgb(10, 59, 4);
      box-shadow: 2px 2px 20px whitesmoke;
  } */

@media (max-width: 1100px) and (min-width: 701px) {
  .MyImg img {
    height: 35vh;
    width: 35vh;
  }
}
@media (max-width: 700px) and (min-width: 600px) {
  .MyImg img {
    height: 29vh;
    width: 29vh;
  }
  .MyImg {
    left: 11vw;
  }
}
@media (max-width: 700px) and (min-width: 501px) {
  .MyImg img {
    height: 25vh;
    width: 25vh;
  }
  .MyImg {
    left: 7vw;
  }
}
@media (max-width: 500px) and (min-width: 451px) {
  .MyImg img {
    height: 22vh;
    width: 19vh;
  }
  .MyImg {
    left: 7vw;
    top: 34vh;
  }
}

@media (max-width: 450px) {
  .MyImg {
    display: none;
  }
}
