.About {
  background-image: linear-gradient(rgba(0, 0, 0, 0.75), rgba(0, 0, 0, 0.85)),
    url("../../assets/images/bg.jpg");
  position: absolute;
  top: 0;
  left: 0;
  height: 100vh;
  z-index: 0;
  width: 100vw;
  background-position: center;
  background-size: cover;
  overflow: hidden;
}

.Buttons {
  width: 100%;
  position: absolute;
  top: 80%;
  display: flex;
  flex-direction: row;
  left: 55%;
}

@media (max-width: 820px) and (min-width: 700px) {
  .Buttons {
    left: 40%;
  }
}
@media (max-width: 700px) and (min-width: 600px) {
  .Buttons {
    left: 27%;
  }
}
@media (max-width: 600px) and (min-width: 450px) {
  .Buttons {
    left: 0%;
  }
}
@media (max-width: 450px) {
  .Buttons {
    display: none;
  }
}
@media (min-width: 450px) {
  .Small {
    display: none;
  }
}
